import React, { useState } from "react";
import axios from "axios";

import Input from '@mui/material/Input';
import LoadingButton from '@mui/lab/LoadingButton';
import SaveIcon from '@mui/icons-material/Save';
import FormLabel from '@mui/material/FormLabel';

export default function ImageUploader(props) {
  const [postImage, setPostImage] = useState({
    imageName: "",
    imageData: ""
  });


  const [loading, setLoading] = React.useState(false);
  const [haveFile, setHaveFile] = React.useState(false);
  
 

//const url = "https://4honb6i2ke.execute-api.us-east-1.amazonaws.com/default/imageupload";
const url ="/upload_handler"
const createImage = (newImage) => axios.post(url, newImage);

  const createPost = async (post) => {
    try {
      const result=await createImage(post);
      console.log(result.data.upload_file)
      let upload_file=result.data.upload_file
      if (upload_file.startsWith("http")){
        props.onChangeImage(result.data.upload_file)
      }else{
      let fullPath = window.location.protocol + "//" + window.location.host+"/"+result.data.upload_file;
      console.log(fullPath)
      props.onChangeImage(fullPath)
        
      }
     
      
    } catch (error) {
      console.log(error.message);
    }
    setLoading(false);
  };

  const handleSubmit = (e) => {
    if (postImage.imageName==""){
        return;
    }
    e.preventDefault();
    setLoading(true);
    createPost(postImage);
  };

  const convertToBase64 = (file) => {
    return new Promise((resolve, reject) => {
      const fileReader = new FileReader();
      fileReader.readAsDataURL(file);
      fileReader.onload = () => {
        resolve(fileReader.result);
      };
      fileReader.onerror = (error) => {
        reject(error);
      };
    });
  };
  const handleFileUpload = async (e) => {
    const file = e.target.files[0];
    const base64 = await convertToBase64(file);
    props.onChangeImage(base64);
    const imageData =base64.replace(/^data:\w+\/\w+;base64,/, '')
    setPostImage({ ...postImage, imageName:file.name,imageData: imageData});
    setHaveFile(true)
    
  };

  return (
    <div>
      <form onSubmit={handleSubmit}>
        <Input
          size="small"
          type="file"
          label="Image"
          name="myFile"
          accept=".jpeg, .png, .jpg"
          color="success"
          onChange={(e) => handleFileUpload(e)}
        />

       
        <FormLabel id="demo-radio-buttons-group-label"><br></br></FormLabel>
        <LoadingButton
        size="small"
        color="success"
        onClick={handleSubmit}
        loading={loading}
        loadingPosition="start"
        startIcon={<SaveIcon />}
        variant="text"
        disabled={!haveFile}
      >
        Upload
      </LoadingButton>
      </form>
    </div>
  );
}