import * as React from 'react';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import GeneratorUI from './GeneratorUI';
import ThemeSwith from './ThemeSwith';
import Copyright from './Copyright';

import { ThemeProvider, createTheme } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import { useSelector } from 'react-redux'

import { BrowserRouter, Route, Routes, Link, Navigate, NavLink  } from "react-router-dom";

import Login from './pages/Login.js';
import Admin from './pages/Admin.js';

import { AuthProvider } from "./hooks/useAuth";


export default function App() {
  const theme = useSelector((state) => state.theme.value)
  const currentTheme = createTheme({
    palette: {
      mode: theme,
    },
  });

  return (

    <BrowserRouter basename="/">
      <AuthProvider>
      <ThemeProvider theme={currentTheme}>

        <CssBaseline />
        <Container maxWidth="lg">
          <Box sx={{ my: 4 }}>
            <Typography variant="h4" component="h1" gutterBottom>
              Stable Diffusion Quick Kit
            </Typography>
            Power by {'© '}
            {' Amazon SageMaker, Amazon Lambda, Cloudfront'}
            <ThemeSwith /> <br></br>
            <Routes>
              <Route path="/login" element={<Login />} />
              <Route path="/admin" element={<Admin />}/>
              <Route path="*" element={<GeneratorUI />} /> 
            </Routes>
            <Copyright />
          </Box>
        </Container></ThemeProvider>
        </AuthProvider>
    </BrowserRouter>
  );
}
